import {useState ,useEffect} from "react";
import axios from "axios";
import Card from 'react-bootstrap/Card';
import 'bootstrap/dist/css/bootstrap.min.css';
import Cookies from 'js-cookie';
import "./App.css"

export default function Transaction_history() {
  let [data, setData] = useState();
  let [item, setItem] = useState([]);
  let [transaction_history,setTransaction_history]=useState([])
  
  let url = "https://back-end-eymw.onrender.com/api/auth/getuserdata";
useEffect(() => {
  const email=Cookies.get('email');
  // console.log(email);
  async function fetchdata() {
    let res = await axios.post(url,{email},{ withCredentials: true });
    let result = [res.data];
    // console.log(result);
    setData(result);
    let newItems = result.map((item) => {
    let name = item.name;
    let email = item.email;
    let balance = item.balance;
    let transaction_history=item.transaction_history;
    return {name,email,balance,transaction_history};
  });
  setItem([...newItems]);
setTransaction_history([...item[0].transaction_history])
  }
  fetchdata();
},[data]);

// console.log(transaction_history);
const table = () =>{
  if(transaction_history.length===0){
    // console.log("sdadsadasdasdqwdqeq");
    return (<h2>NO Transaction history not found!</h2>)
    }else{
      
      return(
        <>
           <table class="table">
        <thead class="thead-dark">
          <tr>
            <th scope="col">No</th>
            <th scope="col">Name</th>
            {/* <th scope="col">Email</th> */}
            <th scope="col">Date</th>
            <th scope="col">Type</th>
            <th scope="col">Amount</th>
            <th scope="col">Total</th>
          </tr>
        </thead>
        <tbody>
        {transaction_history.map((i,index)=>{
      
          return(<tr>
            <th scope="row">{index+1}</th>
            <td>{i.name}</td>
            {/* <td>{i.email}</td> */}
            <td>{i.date}</td>
            <td>{i.type}</td>
            <td>{i.amount}</td>
            <td>{i.total}</td>
            </tr>)
        })}</tbody>
      </table>
              <br></br><br></br>
        </>
      )
    }
}

  
  return (
    <>
   <center>
    {/* <Card className="card" style={{ width: '80%',marginTop:'4%',boxShadow:'0px 0px 10px 0px black' ,color:"black"}}> */}
    <Card className="card" >
    <h1>Transaction history</h1><br></br>
    <Card.Text>
    
      <h4>{} </h4>
        </Card.Text>  
      <Card.Body>
     {table()}
      </Card.Body>
    </Card></center>
    </>
  );
}