
import styles from "./styles.module.css";
import axios from "axios";

const Main = () => {
	const handleLogout = async  () => {
		try {
			await axios.post('http://localhost:8080/api/auth/clearCookie',{},{ withCredentials: true }); // Send a POST request to your server's /clearCookie endpoint
			alert('Cookie cleared successfully');
			window.location='/login';
		  } catch (error) {
			console.error('Failed to clear cookie:', error);
		  }
		// localStorage.removeItem("token");
		
	};
	const getuser = async  () => {
		try {
			await axios.get('http://localhost:8080/api/auth/getuser',{},{ withCredentials: true }); // Send a POST request to your server's /clearCookie endpoint
			alert('Cookie get successfully');
			// window.location='/login';
		  } catch (error) {
			console.error('Failed to get cookie:', error);
		  }
		// localStorage.removeItem("token");
		
	};

	return (
		<div className={styles.main_container}>
			<nav className={styles.navbar}>
				<h1>fakebook</h1>
				<button className={styles.white_btn} onClick={handleLogout}>
					Logout
				</button>
				
			</nav>
			<button onClick={getuser}>
					getuser
				</button>
		</div>
	);
};

export default Main;
