// export default Login;
import { toast } from 'react-hot-toast';
import React, { useState } from 'react';
import axios from 'axios';
import { Link } from "react-router-dom";
import styles from "./styles.module.css";
import Cookies from 'js-cookie';


const Login = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  const handleLogin = async () => {
    try {
      const response = await axios.post(
        'https://back-end-eymw.onrender.com/api/auth',
        { email, password } // Include cookies in the request
      );
	
	//   toast.error('Login failed.  check your credentials.');
      if (response.status === 200) {
        toast.error(response.data.error);
      }
      if (response.status === 201) {
        toast.success(response.data.message);
		console.log(response.data.token);
		console.log(response.data.email);
		console.log(response.data.people);
		Cookies.set('AuthToken', response.data.token);
		Cookies.set('email', response.data.email);
		Cookies.set('people', response.data.user);
		window.location = "/";
      }
	 
    } catch (error) {
		toast.error('Login failed.  check your credentials.');
    }
  };
//   const get = async () => {
// 	try {
// 		await axios.post('http://localhost:8080/api/auth//clearCookie',{},{ withCredentials: true }); // Send a POST request to your server's /clearCookie endpoint
// 		alert('Cookie cleared successfully');
// 		// window.location='/login';
// 	  } catch (error) {
// 		console.error('Failed to clear cookie:', error);
// 	  }
//   };

  return (
	<div className={styles.login_container}>
			<div className={styles.login_form_container}>
				<div className={styles.left}>
    <div className={styles.form_container}>
      <h2>Login</h2>
      <input
        type="text"
        placeholder="Email"
        value={email}
        onChange={(e) => setEmail(e.target.value)}
		className={styles.input}
      />
      <input
        type="password"
        placeholder="Password"
        value={password}
        onChange={(e) => setPassword(e.target.value)}
		className={styles.input}
      />
      <button onClick={handleLogin} className={styles.green_btn}>Login</button>
       {/* <button onClick={get}>get</button> */}
	   </div>
	   </div>
	   <div className={styles.right}>
					<h1>New Here ?</h1>
					<Link to="/signup">
						<button type="button" className={styles.white_btn}>
							Sing Up
						</button>
					</Link>
				</div>
    </div>
    </div>
  );
};

export default Login;
