import { useState ,useEffect} from "react";
import axios from "axios";
import { toast } from 'react-hot-toast';
import styles from "./styles.module.css";
import 'bootstrap/dist/css/bootstrap.min.css';
import { Button } from "react-bootstrap";
import Cookies from 'js-cookie';
export default function Deposit() {
 
  let [withdraw, setWithdraw] = useState();
  let [data, setData] = useState();
  let [item, setItem] = useState([]);
  
  let url = "https://back-end-eymw.onrender.com/api/auth/getuserdata";
useEffect(() => {
  const email=Cookies.get('email');
  // console.log(email);
  async function fetchdata() {
    let res = await axios.post(url,{email},{ withCredentials: true });
    let result = [res.data];
    // console.log(result);
    setData(result);
    let newItems = result.map((item) => {
    let name = item.name;
    let email = item.email;
    let balance = item.balance;
    return {name,email,balance};
  });
  setItem([...newItems]);
  }
  fetchdata();
},[data]);
  
 const tata= item.map((i,index)=>{
    // console.log(i);
      return `${i.name} balance is : ${i.balance}`
  })
  
  const handleSubmit = async (event) => {
    event.preventDefault();
    item.map((i,index) =>{
      if(i.balance>=withdraw){
        const balance= parseInt(i.balance)-parseInt(withdraw)
        const email=item[0].email;
        const th={
          name:i.name,
          email:i.email,
          amount:`- ${withdraw}`,
          total:balance,
          type:"withdraw"
         }
           console.log(data.balance);
           const response = axios.post(
            'https://back-end-eymw.onrender.com/api/users/withdraw',
            { email, balance,th },
            { withCredentials: true })
         
           setWithdraw("")
           toast.success(`Amount ${withdraw} is withdraw Successfully`);
      //   console.log(i.index);
      //   const data = {
      //     balence: parseInt(i.balence)-parseInt(withdraw)
      //  }
      }else{
        toast.error(`Balanced Inefficient`);
      }
        // const balance= parseInt(i.balance)-parseInt(withdraw)
       
    }) 
  }
  
  return (
    <>
      <div className={styles.login_container}>
			<div className={styles.login_form_container}>
				<div className={styles.left}>
    <div className={styles.form_container}>
      <h1>Withdraw</h1>
      <h4>{tata} </h4>
      <input
        type="text"
        placeholder="Enter Amount"
        value={withdraw}
        onChange={(e) => setWithdraw(e.target.value)}
		className={styles.input}
      />
      <Button type="submit" className={styles.green_btn} disabled={!(withdraw)} onClick={handleSubmit} >Withdraw</Button>
      {/* <button onClick={handleSubmit}  disabled={!(deposit)} className={styles.green_btn}>Login</button> */}
       {/* <button onClick={get}>get</button> */}
	   </div>
	   </div>
    </div>
    </div>
    </>
  );
}